@import "https://fonts.googleapis.com/css2?family=Roboto:wght@100;300&display=swap";
* {
  box-sizing: border-box;
  color: #888;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

body {
  background-color: #f2f2f2;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.header-text {
  color: #ccc;
  font-size: 100px;
  font-weight: 100;
}

.main {
  max-width: 562px;
  width: 100%;
  background-color: #fff;
  box-shadow: 0 2px 4px #0003;
}

.main_parent {
  position: relative;
}

.input-new-task {
  height: 63px;
  border-bottom: 1px solid #e5e5e5;
  display: flex;
}

.input-new-task_btn-select-all {
  cursor: pointer;
  background-color: #fff;
  background-image: url("select-all.a2c75cf5.svg");
  background-position: center;
  background-repeat: no-repeat;
  border: 0;
  margin: 21px 10px;
  padding: 10px 15px;
  font-size: 0;
}

.input-new-task_input_w_placeholder {
  height: 100%;
  width: 100%;
  color: #ccc;
  border: 0;
  margin-right: 0;
  font-size: 24px;
  font-style: italic;
  font-weight: 300;
}

.input-new-task_input_w_placeholder:focus {
  color: #666;
  font-style: inherit;
}

.input-new-task_label {
  font-size: 0;
}

.tasks-block {
  max-height: 296px;
  overflow-y: auto;
}

.tasks-block_item {
  cursor: pointer;
  border-bottom: 1px solid #e5e5e5;
  padding: 12px 10px;
  display: flex;
}

.tasks-block_item:hover {
  background-color: #fafafa;
}

.tasks-block_item_complete input {
  appearance: none;
  width: 36px;
  height: 36px;
  background-color: #fff;
  border: 1px solid #e6e6e6;
  border-radius: 50%;
  align-self: center;
}

.tasks-block_item_complete input:focus, .tasks-block_item_complete input:hover {
  border: 1px solid #60c2b1;
}

.tasks-block_item_complete input:checked {
  background-image: url("ok.de4fb9c4.svg");
  background-position: center;
  background-repeat: no-repeat;
  border: 1px solid #60c2b1;
}

.tasks-block_item_label {
  font-size: 0;
}

.tasks-block_item_task_text {
  color: #666;
  align-self: center;
  margin-right: auto;
  font-size: 24px;
  font-weight: 300;
}

.tasks-block_item_task_delete {
  opacity: 0;
  cursor: pointer;
  background-color: #fff;
  background-image: url("delete.9c64ba22.svg");
  background-position: center;
  background-repeat: no-repeat;
  border: 0;
  padding: 15px;
  font-size: 0;
}

.tasks-block_item_task_delete:focus {
  opacity: inherit;
}

.tasks-block_item:hover > .tasks-block_item_task_delete {
  opacity: inherit;
  background-color: #fafafa;
}

.footer {
  height: 40px;
  justify-content: space-between;
  align-items: center;
  margin: 0 10px;
  display: flex;
}

.footer_items-left {
  font-size: 14px;
}

.footer_tasks-sorting {
  display: flex;
}

.footer_tasks-sorting_input {
  padding: 5px;
}

.footer_tasks-sorting_input input {
  appearance: none;
}

.footer_tasks-sorting_input_label {
  border: 1px solid #0000;
  border-radius: 10%;
  padding: 3px 10px;
  display: inline-block;
}

.footer_tasks-sorting_input input:checked + .footer_tasks-sorting_input_label {
  border: 1px solid #ebdfdf;
}

.footer_tasks-sorting_input input:hover + .footer_tasks-sorting_input_label, .footer_tasks-sorting_input input:focus + .footer_tasks-sorting_input_label {
  border: 1px solid #e5e5e5;
}

.footer_clear button {
  background-color: #fff;
  border: 0;
  padding: 5px;
}

.footer_clear button:hover {
  cursor: pointer;
  color: #000;
}

.main_child {
  height: 5px;
  margin: 0 auto;
  position: absolute;
  top: 100%;
  left: 2px;
  right: 2px;
  box-shadow: 0 2px 4px #0003;
}

